import { _decorator, color, Component, find, instantiate, Label, Node, Prefab, Sprite, v3, Animation, game, director, RigidBody2D, isValid, BoxCollider2D, math, v2, UIOpacity, tween, animation, log, Layout, sys, ScrollView, Color, BlockInputEvents, Button, Vec3, RichText } from 'cc';
import { ConfigShareState, ConfigSidebarState, Game_Script_Template } from './Game_Script_Template';
import { Game_Mode, Resoure_Data } from '../Tool/Resoure_Data';
import { Tool_Event } from '../Tool/Tool_Event';
import { Tool_Other } from '../Tool/Tool_Other';
import { Tool_Animation } from '../Tool/Tool_Animation';
import { Tool_UI } from '../Tool/Tool_UI';
import { Tool_Console } from '../Tool/Tool_Console';
import { PlatformApi } from '../Other/SDK/PlatformApi';
import { HttpRequest } from '../Other/SDK/HttpRequest';
const { ccclass, property } = _decorator;



@ccclass('Game_Script')
export class Game_Script extends Game_Script_Template {
    IsMove: boolean = false;
    /**省份数 */
    NumberOfProvinces: number = 34;

    /**是否提示 */
    IsHint: boolean = false;
    /**是否复活 */
    IsContinue: boolean = false;

    //--------------------------------------------------------------------------------------------

    /**主界面视图 */
    /**设置按钮 */
    VM_BtnSetting: Node = null;
    //--------------------------------------------------------------------------------------------

    /**正常模式视图 */
    ViewNormal: Node = null;
    VN_Content: Node = null;
    VN_Map: Node = null;
    /**设置按钮 */
    VN_BtnSetting: Node = null;

    //--------------------------------------------------------------------------------------------

    /**更多主题面板 */
    PanelMore: Node = null;
    /**设置按钮 */
    PM_BtnSetting: Node = null;



    //--------------------------------------------------------------------------------------------

    /**设置面板 */
    PanelSetting: Node = null;
    /**音乐开关按钮 */
    PS_MusicSwitch: Node = null;
    /**声音开关按钮 */
    PS_SoundSwitch: Node = null;
    /**震动开关按钮 */
    PS_VibrateSwitch: Node = null;

    /**返回游戏 */
    PS_BtnBack: Node = null;
    /**退出游戏 */
    PS_BtnExit: Node = null;
    /**关闭按钮 */
    PS_BtnClose: Node = null;

    //--------------------------------------------------------------------------------------------

    /**道具面板 */
    PanelProp: Node = null;
    /**标题文本 */
    PP_LabeTitle: Label = null;
    /**数量文本 */
    PP_LabeNumber: Label = null;
    /**关闭按钮 */
    PP_BtnClose: Node = null;
    /**确定按钮 */
    PP_BtnOk: Node = null;

    //--------------------------------------------------------------------------------------------

    /**地区面板 */
    PanelRegionl: Node = null;
    /**地区父节点 */
    RegionlParent: Node = null;
    /**关闭按钮 */
    PR_BtnClose: Node = null;
    /**确定按钮 */
    PR_BtnOk: Node = null;
    /**上一次地区 */
    PR_RegionlLast: Node = null;
    /**当前地区 */
    PR_Regionl: Node = null;

    //--------------------------------------------------------------------------------------------

    /**游戏面板 */
    PanelGame: Node = null;

    //--------------------------------------------------------------------------------------------

    /**成语面板 */
    PanelIdiom: Node = null;

    //--------------------------------------------------------------------------------------------

    /**影视面板 */
    PanelFilm: Node = null;

    //--------------------------------------------------------------------------------------------

    /**找演员面板 */
    PanelFindActor: Node = null;

    //--------------------------------------------------------------------------------------------

    /**排行榜面板 */
    PanelRank: Node = null;
    /**关闭按钮 */
    PRK_BtnClose: Node = null;
    /**好友排行 */
    PRK_RankingFriends: Node = null;
    /**世界排行 */
    PRK_RankingWorld: Node = null;
    /**好友排行按钮 */
    PRK_BtnRankingFriends: Node = null;
    /**世界排行按钮 */
    PRK_BtnRankingWorld: Node = null;
    /**好友排行数 */
    RankNumberF: number = 20;
    /**世界排行数 */
    RankNumberW: number = 100;

    //--------------------------------------------------------------------------------------------

    /**足迹面板 */
    PanelFootPrints: Node = null;
    /**关闭按钮 */
    PFP_BtnClose: Node = null;

    //--------------------------------------------------------------------------------------------

    /**侧边栏面板 */
    PanelSidebar: Node = null;
    /**确定按钮 */
    PD_BtnOk: Node = null;
    /**关闭按钮 */
    PD_BtnClose: Node = null;
    /**获取按钮 */
    PD_BtnGet: Node = null;

    //--------------------------------------------------------------------------------------------

    /**邀请面板 */
    PanelInvite: Node = null;
    /**关闭按钮 */
    PI_BtnClose: Node = null;
    /**邀请按钮 */
    PI_BtnInvite: Node = null;
    /**获取按钮 */
    PI_BtnGet: Node = null;
    /**刷新按钮 */
    PI_BtnUpdate: Node = null;

    //--------------------------------------------------------------------------------------------
    @property({ type: Node })
    Message: Node = null;
    @property({ type: Label })
    MessageLabel: Label = null;

    /**动画开关 */
    AnimSwitch: boolean = false;

    constructor() {
        super();
    }

    onLoad() {
        super.onLoad();
        PlatformApi.instance.showInterstitialAd();
    }

    async start() {
        super.start();
        this.BlockInputEvents.active = true;
        await this.SceneLoad();
        this.BlockInputEvents.active = false;
        console.log("播放音乐");
        this.Resoure_Data.Music_Play("Audio/Sound/BgmGame");
        // console.log(HttpRequest.instance.getOpenId());
        // console.log("-----------getOpenId------------");
    }

    /**注册设置面板 */
    OnPanelSetting(AnimSwitch: boolean = false) {
        let First_Start = this.Resoure_Data.Data_Get("First_Start");
        if (First_Start) {
            First_Start = false;
            this.Resoure_Data.Data_Set("First_Start", First_Start);
        }

        console.log("初始音乐");
        this.Resoure_Data.Music_Switch_Set(this.Resoure_Data.Music_Switch_Get());
        this.Resoure_Data.Sound_Switch_Set(this.Resoure_Data.Sound_Switch_Get());
        this.Resoure_Data.Vibration_Switch_Set(this.Resoure_Data.Vibration_Switch_Get());

        this.PS_MusicSwitch.getChildByName("On").active = this.Resoure_Data.Music_Switch_Get();
        this.PS_MusicSwitch.getChildByName("Off").active = !this.Resoure_Data.Music_Switch_Get();
        this.PS_SoundSwitch.getChildByName("On").active = this.Resoure_Data.Sound_Switch_Get();
        this.PS_SoundSwitch.getChildByName("Off").active = !this.Resoure_Data.Sound_Switch_Get();
        this.PS_VibrateSwitch.getChildByName("On").active = this.Resoure_Data.Vibration_Switch_Get();
        this.PS_VibrateSwitch.getChildByName("Off").active = !this.Resoure_Data.Vibration_Switch_Get();

        console.log("注册设置面板");

        /**音乐开关按钮 */
        const FunPSMusicSwitch = () => {
            this.Resoure_Data.Sound_Play("Audio/Sound/Btn");
            this.Resoure_Data.Music_Switch_Set(!this.Resoure_Data.Music_Switch_Get());
            this.PS_MusicSwitch.getChildByName("On").active = this.Resoure_Data.Music_Switch_Get();
            this.PS_MusicSwitch.getChildByName("Off").active = !this.Resoure_Data.Music_Switch_Get();
        }
        /**声音开关按钮 */
        const FunPSSoundSwitch = () => {
            this.Resoure_Data.Sound_Switch_Set(!this.Resoure_Data.Sound_Switch_Get());
            this.PS_SoundSwitch.getChildByName("On").active = this.Resoure_Data.Sound_Switch_Get();
            this.PS_SoundSwitch.getChildByName("Off").active = !this.Resoure_Data.Sound_Switch_Get();
            this.Resoure_Data.Sound_Play("Audio/Sound/Btn");
        }
        /**震动开关按钮 */
        const FunPSVibrateSwitch = () => {
            this.Resoure_Data.Sound_Play("Audio/Sound/Btn");
            this.Resoure_Data.Vibration_Switch_Set(!this.Resoure_Data.Vibration_Switch_Get());
            this.PS_VibrateSwitch.getChildByName("On").active = this.Resoure_Data.Vibration_Switch_Get();
            this.PS_VibrateSwitch.getChildByName("Off").active = !this.Resoure_Data.Vibration_Switch_Get();
        }
        /**关闭按钮 */
        const FunPSBtnClose = () => {
            FunPSBtnExit();
        }
        /**放弃挑战 */
        const FunPSBtnExit = () => {
            this.Resoure_Data.Sound_Play("Audio/Sound/Btn");
            this.PanelGameHide();
            this.ViewHide();
        }
        /**返回游戏 */
        const FunPSBtnBack = () => {
            this.Resoure_Data.Sound_Play("Audio/Sound/Btn");
            this.PanelSetting.active = false;
        }
        this.PS_MusicSwitch = find("Window/MusicLayout/Switch", this.PanelSetting);
        this.PS_SoundSwitch = find("Window/SoundLayout/Switch", this.PanelSetting);
        this.PS_VibrateSwitch = find("Window/VibrateLayout/Switch", this.PanelSetting);
        this.PS_BtnClose = find("BtnClose", this.PanelSetting);
        this.PS_BtnBack = find("BtnLayout/BtnBack", this.PanelSetting);
        this.PS_BtnExit = find("BtnLayout/BtnExit", this.PanelSetting);

        /**设置按钮事件 */
        Tool_Event.instance.On_Event_TOUCH_END(this.PS_MusicSwitch, FunPSMusicSwitch);
        Tool_Event.instance.On_Event_TOUCH_END(this.PS_SoundSwitch, FunPSSoundSwitch);
        Tool_Event.instance.On_Event_TOUCH_END(this.PS_VibrateSwitch, FunPSVibrateSwitch);
        Tool_Event.instance.On_Event_TOUCH_END(this.PS_BtnClose, FunPSBtnClose);
        Tool_Event.instance.On_Event_TOUCH_END(this.PS_BtnBack, FunPSBtnBack);
        Tool_Event.instance.On_Event_TOUCH_END(this.PS_BtnExit, FunPSBtnExit);
        /**设置按钮动画 */
        Tool_Event.instance.Set_Btn_Event_Anim(this.PS_BtnClose, AnimSwitch);
        Tool_Event.instance.Set_Btn_Event_Anim(this.PS_BtnBack, AnimSwitch);
        Tool_Event.instance.Set_Btn_Event_Anim(this.PS_BtnExit, AnimSwitch);
    }
    /**注册道具面板 */
    OnPanelProp(AnimSwitch: boolean = false) {
        /**返回按钮 */
        const FunBack = () => {
            this.Resoure_Data.Sound_Play("Audio/SoundBtn");
            this.PanelGameHide();
            this.ViewHide();
        }
        /**设置按钮事件 */
        Tool_Event.instance.On_Event_TOUCH_END(this.PP_BtnClose, FunBack);
        Tool_Event.instance.On_Event_TOUCH_END(this.PP_BtnOk, FunBack);
        /**设置按钮动画 */
        Tool_Event.instance.Set_Btn_Event_Anim(this.PP_BtnClose, AnimSwitch);
        Tool_Event.instance.Set_Btn_Event_Anim(this.PP_BtnOk, AnimSwitch);
    }
    /**注册地区面板 */
    OnPanelRegionl(AnimSwitch: boolean = false) {
        /**关闭按钮 */
        const FunPRBtnClose = () => {
            this.Resoure_Data.Sound_Play("Audio/SoundBtn");
            /**获取数据 */
            super.DataGet();
            Tool_UI.instance.Pool_Put(this.PanelRegionl);
        }
        /**确定按钮 */
        const FunPRBtnOk = () => {
            this.Resoure_Data.Sound_Play("Audio/SoundBtn");
            /**保存数据 */
            super.DataSet();
            Tool_UI.instance.Pool_Put(this.PanelRegionl);
        }
        /**设置按钮事件 */
        Tool_Event.instance.On_Event_TOUCH_END(this.PR_BtnClose, FunPRBtnClose);
        Tool_Event.instance.On_Event_TOUCH_END(this.PR_BtnOk, FunPRBtnOk);
        /**设置按钮动画 */
        Tool_Event.instance.Set_Btn_Event_Anim(this.PR_BtnClose, AnimSwitch);
        Tool_Event.instance.Set_Btn_Event_Anim(this.PR_BtnOk, AnimSwitch);
    }

    /**注册排行榜面板 */
    OnPanelRank(AnimSwitch: boolean = false) {
        /**关闭按钮 */
        const FunPRCBtnClose = () => {
            this.Resoure_Data.Sound_Play("Audio/SoundBtn");
            Tool_UI.instance.Pool_Put(this.PanelRank);
        }
        /**好友排行按钮 */
        const FunPRCBtnRankingFriends = () => {
            this.Resoure_Data.Sound_Play("Audio/SoundBtn");
            Tool_UI.instance.Set_Color(this.PRK_BtnRankingWorld, color(155, 155, 155, 255));
            Tool_UI.instance.Set_Color(this.PRK_BtnRankingFriends, color(255, 255, 255, 255));

            Tool_UI.instance.Set_UIOpacity(this.PRK_RankingWorld, 0);
            Tool_UI.instance.Set_UIOpacity(this.PRK_RankingFriends, 255);

            Tool_UI.instance.Component_Off(this.PRK_RankingWorld, ScrollView);
            Tool_UI.instance.Component_On(this.PRK_RankingFriends, ScrollView);
        }
        /**世界排行按钮 */
        const FunPRCRankingWorld = () => {
            this.Resoure_Data.Sound_Play("Audio/SoundBtn");
            Tool_UI.instance.Set_Color(this.PRK_BtnRankingFriends, color(155, 155, 155, 255));
            Tool_UI.instance.Set_Color(this.PRK_BtnRankingWorld, color(255, 255, 255, 255));

            Tool_UI.instance.Set_UIOpacity(this.PRK_RankingFriends, 0);
            Tool_UI.instance.Set_UIOpacity(this.PRK_RankingWorld, 255);

            Tool_UI.instance.Component_Off(this.PRK_RankingFriends, ScrollView);
            Tool_UI.instance.Component_On(this.PRK_RankingWorld, ScrollView);
        }
        /**设置按钮事件 */
        Tool_Event.instance.On_Event_TOUCH_END(this.PRK_BtnClose, FunPRCBtnClose);
        Tool_Event.instance.On_Event_TOUCH_END(this.PRK_BtnRankingFriends, FunPRCBtnRankingFriends);
        Tool_Event.instance.On_Event_TOUCH_END(this.PRK_BtnRankingWorld, FunPRCRankingWorld);
        /**设置按钮动画 */
        Tool_Event.instance.Set_Btn_Event_Anim(this.PRK_BtnClose, AnimSwitch);
        Tool_Event.instance.Set_Btn_Event_Anim(this.PRK_BtnRankingFriends, AnimSwitch);
        Tool_Event.instance.Set_Btn_Event_Anim(this.PRK_BtnRankingWorld, AnimSwitch);
    }

    /**场景加载 */
    async SceneLoad(): Promise<void> {
        return new Promise(async (resolve, reject) => {
            Tool_UI.instance.Set_UIOpacity(this.Message, 0);
            /**加载设置界面 */
            await this.LoadPanelSetting();
            resolve();
        });
    }

    /**加载设置面板 */
    LoadPanelSetting(): Promise<void> {
        return new Promise(async (resolve, reject) => {
            /**加载预制体 */
            let PrefabPanelSetting = await this.Resoure_Data.Load_Prefab("Prefab/PanelSetting");
            this.PanelSetting = Tool_UI.instance.Set_Prefab_Node(PrefabPanelSetting, this.InterfaceView, v3(0, 0, 0), "");
            this.PanelSetting.active = false;
            this.PS_MusicSwitch = find("Window/MusicLayout/Switch", this.PanelSetting);
            this.PS_SoundSwitch = find("Window/SoundLayout/Switch", this.PanelSetting);
            this.PS_VibrateSwitch = find("Window/VibrateLayout/Switch", this.PanelSetting);
            this.PS_BtnClose = find("BtnClose", this.PanelSetting);
            this.PS_BtnExit = find("BtnLayout/BtnMain", this.PanelSetting);
            this.PS_BtnBack = find("BtnLayout/BtnContinue", this.PanelSetting);
            /**注册设置面板 */
            this.OnPanelSetting(this.AnimSwitch);
            resolve();
        });
    }

    /**加载道具面板 */
    LoadPanelProp(): Promise<void> {
        return new Promise(async (resolve, reject) => {
            /**加载预制体 */
            let PrefabPanelProp = await this.Resoure_Data.Load_Prefab("Prefab/PanelProp");
            this.PanelProp = Tool_UI.instance.Set_Prefab_Node(PrefabPanelProp, this.InterfaceView, v3(0, 0, 0), "");
            this.PanelProp.active = false;
            /**绑定关键节点 */
            this.PP_LabeTitle = find("Window/Title", this.PanelProp).getComponent(Label);
            this.PP_LabeNumber = find("Window/Icon/Number", this.PanelProp).getComponent(Label);
            this.PP_BtnClose = find("Window/BtnClose", this.PanelProp);
            this.PP_BtnOk = find("Window/BtnOk", this.PanelProp);
            /**注册道具面板 */
            this.OnPanelProp(this.AnimSwitch);
            resolve();
        });
    }


    /**加载成语面板 */
    LoadPanelIdiom(): Promise<void> {
        return new Promise(async (resolve, reject) => {
            /**加载预制体 */
            let PrefabPanelIdiom = await this.Resoure_Data.Load_Prefab("Prefab/PanelIdiom/PanelIdiom");
            this.PanelIdiom = Tool_UI.instance.Set_Prefab_Node(PrefabPanelIdiom, this.InterfaceView, v3(0, 0, 0), "");
            resolve();
        });
    }



    /**获取道具 */
    async GetProp(Value: number = 1, Text: string = "获得奖励") {
        if (this.PanelProp == null) {
            /**加载道具面板 */
            await this.LoadPanelProp();
        }
        this.PropNum += Value;
        super.DataSet();
        find("Window/Icon/Number", this.PanelProp).getComponent(Label).string = `x${Value}`;
        Tool_UI.instance.Node_Index_Set(this.PanelProp, -1);
        this.PanelProp.active = true;
        Tool_Animation.instance.Anim_Show_Window(this.PanelProp.getChildByName("Window"));
    }

    /**游戏面板隐藏 */
    async PanelGameHide() {
        if (this.PanelGame != null) {
            this.PanelGame.active = false;
        }
    }


    /**视图隐藏 */
    ViewHide() {
        Tool_UI.instance.Hide_Node_Children(this.InterfaceView);
    }


    /**显示消息 */
    OnMessage(Text: string = "", Time: number = 2) {
        Tool_Animation.instance.Tween_Stop_Tag(1);
        this.MessageLabel.string = Text;
        Tool_Animation.instance.Anim_Show_Node(this.Message);
        Tool_Animation.instance.Async_Function_Id(Time, 1, () => {
            this.OffMessage();
        })
    }
    /**隐藏消息 */
    OffMessage() {
        Tool_Animation.instance.Anim_Hide_Node(this.Message);
    }

}

